// Функція плавної прокрутки
function smoothScroll(targetId, duration = 800) {
  const target = document.querySelector(targetId);
  if (!target) {
    console.error(`Element with id "${targetId}" not found`);
    return;
  }

  const start = window.pageYOffset;
  const end = target.offsetTop;
  const distance = end - start;
  let startTime = null;

  function animation(currentTime) {
    if (!startTime) startTime = currentTime;
    const timeElapsed = currentTime - startTime;
    const progress = Math.min(timeElapsed / duration, 1);
    const ease =
      progress < 0.5
        ? 4 * progress * progress * progress
        : 1 - Math.pow(-2 * progress + 2, 3) / 2;

    window.scrollTo(0, start + distance * ease);
    if (timeElapsed < duration) requestAnimationFrame(animation);
  }

  requestAnimationFrame(animation);
}

// Обробка кнопки welcome після завантаження DOM
function initWelcomeButton() {
  const welcomeButton = document.querySelector('.welcome-button');
  if (welcomeButton) {
    welcomeButton.addEventListener('click', function (e) {
      e.preventDefault();
      smoothScroll('#location', 1000);
    });
  } else {
    console.error('Welcome button not found');
  }
}


// Ініціалізація всіх функцій після завантаження DOM
function initApp() {
  initWelcomeButton();
  initMobileMenu();
  initSubscriptionForm();
}

// Обробка мобільного меню
function initMobileMenu() {
  const refs = {
    openModalBtn: document.querySelector('[data-modal-open]'),
    closeModalBtn: document.querySelector('[data-modal-close]'),
    modal: document.querySelector('[data-modal]'),
  };

  if (!refs.openModalBtn || !refs.closeModalBtn || !refs.modal) {
    console.error('Modal elements not found:', {
      openModalBtn: refs.openModalBtn,
      closeModalBtn: refs.closeModalBtn,
      modal: refs.modal,
    });
    return;
  }

  function toggleModal() {
    refs.modal.classList.toggle('is-open');
  }

  refs.openModalBtn.addEventListener('click', toggleModal);
  refs.closeModalBtn.addEventListener('click', toggleModal);
}

// Обробка форми підписки
function initSubscriptionForm() {
  const subscribeForm = document.querySelector('.subscribe-form');
  const successModal = document.querySelector('.backdrop');
  const successCloseBtns = document.querySelectorAll(
    '.success-btn-x, .success-btn-close'
  );

  if (!subscribeForm || !successModal) {
    console.error('Subscription form or success modal not found');
    return;
  }

  subscribeForm.addEventListener('submit', function (e) {
    e.preventDefault();
    // Показуємо модалку успіху
    successModal.classList.add('subscription-open');
  });

  // Закриття модалки успіху
  successCloseBtns.forEach(btn => {
    btn.addEventListener('click', function () {
      successModal.classList.remove('subscription-open');
    });
  });
}

// Запуск додатку після завантаження DOM
if (document.readyState === 'loading') {
  document.addEventListener('DOMContentLoaded', initApp);
} else {
  initApp();
}
